.lib-gallery {
  margin-left: auto;
  margin-right: auto;

  width: 320px;

  padding-top: 20px;
  padding-left: 20px;
  padding-right: 20px;

  @media screen and (min-width: 768px) {
    width: 768px;
    padding-top: 60px;
    padding-left: 32px;
    padding-right: 32px;
  }

  @media screen and (min-width: 1280px) {
    width: 1280px;
    padding-top: 60px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

.lib-gallery__set {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  @media screen and (min-width: 768px) {
    margin-left: -32px;
    margin-bottom: -32px;
  }

  @media screen and (min-width: 1280px) {
    margin-left: -16px;
    margin-bottom: -32px;
  }
}
.plug{
  margin-left: auto;
  margin-right: auto;
}
