.header {
  // background-image: url('../images/abstract-background-with-red-lines\ 1.jpg');
  background-color: rgb(0, 0, 0, 0.5);
  background-repeat: no-repeat;
  background-size: cover;

  height: 215px;

  @media screen {
    height: 230px;
    background-image:
      url('../images/home-header-background/abstract-background-with-red-lines\ 320px.jpg');

    @media screen and (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
      background-image:
        url('../images/home-header-background/abstract-background-with-red-lines\ 320px@2.jpg');
    }
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    background-image:
      url('../images/home-header-background/abstract-background-with-red-lines\ 768px.jpg');

    @media screen and (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
      background-image:
        url('../images/home-header-background/abstract-background-with-red-lines\ 768px@2.jpg');

    }
  }

  @media screen and (min-width: 1280px) {
    background-image:
      url('../images/home-header-background/abstract-background-with-red-lines\ 1280px.jpg');

    @media screen and (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
      background-image:
        url('../images/home-header-background/abstract-background-with-red-lines\ 1280px@2.jpg');
    }
  }
}

.header-film__container {
  flex-wrap: nowrap;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;

  @media screen and (min-width: 768px) {
    padding-bottom: 20px;
  }
}

.header-film__main-wraper {
  margin-bottom: 40px;
  padding-top: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-film__logo-link {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row-reverse;
}

.header-film__logo-name {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 30px;
  line-height: 1.16667;
  display: flex;
  align-items: center;
  text-align: center;

  color: var(--main-background-color);

  @media screen and (max-width: 767.98px) {
    display: none;
  }

}

.header-film__logo {
  cursor: pointer;
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;

  color: rgba(255, 255, 255, 1);
  font-weight: 500;
  font-size: 30px;
  line-height: 1.16;
}

.header-film__logo-svg {
  margin-right: 10px;
}

// navigation
.header-film-film__navigation {

  display: flex;
  align-items: center;
}

.header-film__navigation-button {
  display: flex;
  justify-content: space-between;

}

.header-film__navigation-item {
  margin-right: 40px;
}

.header-film__navigation-item:nth-child(2) {
  margin-right: 0;
}

.header-film__navigation-link {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
}


.header-film__uncurrent {
  position: relative;
  height: 100%;
  padding-bottom: 1px;

  &::after {
    content: '';
    position: absolute;
    bottom: -1px;
    display: block;
    width: 100%;
    height: 3px;
    background-color: var(--accent-color-bright);
    transform: scaleX(0);
    transition: transform 250ms ease-in-out;
  }
}

.header-film__uncurrent:hover::after {
  transform: scaleX(1);

}

.header-film__navigation-link {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;

  display: flex;
  text-transform: uppercase;
  color: var(--main-background-color);
}


.header-film__logo-link:hover>.header-film__logo-svg,
.header-film__logo-link:focus>.header-film__logo-svg {
  animation: rotate-vert-center 500ms cubic-bezier(0.4, 0, 0.2, 1) 1;
}

.main-current {
  position: relative;
  height: 100%;
  padding-bottom: 1px;


  &::after {
    content: '';
    position: absolute;
    bottom: -1px;
    display: block;
    width: 100%;
    height: 3px;
    background-color: var(--accent-color-bright);
  }
}

.header-film__search-wraper {
  margin: auto;
  position: relative;
  width: 380px;

  @media screen and (max-width: 767px) {
    width: 280px;
    padding-right: 20px;
  }

  @media screen and (min-width: 768px) {
    width: 330px;
  }
}

.header-film__form {
  display: flex;
  position: relative;
  margin: auto;
}

.header-film__input {
  position: relative;
  width: 100%;
  border: none;
  border-bottom: 0.5px solid var(--secondary-text-color);
  padding: 3px 16px 3px 1px;
  outline: none;
  background-color: transparent;
  color: var(--secondary-text-color);
}

.header__search-icon {
  position: absolute;
  right: 0px;
}

.header-film__warning-msg {
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  margin-top: 16px;
  margin-bottom: 45px;

  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14;
  color: var(--accent-color-bright);
}

.header__form-search {
  background-color: transparent;
  border: none;
  cursor: pointer;
  display: flex;
  padding: 0;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

}

.header__form-search:hover,
.header__form-search:focus {
  transform: scale(1.25);
}